import { html, customElement } from "@umbraco-cms/backoffice/external/lit";
import { UmbCollectionDefaultElement } from "@umbraco-cms/backoffice/collection";
import "./form-entry-collection-toolbar.element.js";
import "./views/table/form-entry-table-collection-view.element.js";

const elementName = "form-entry-collection";

@customElement(elementName)
export class FormEntryCollectionElement extends UmbCollectionDefaultElement {
  protected renderToolbar() {
    return html`<form-entry-collection-header
      slot="header"
    ></form-entry-collection-header> `;
  }
}

export default FormEntryCollectionElement;

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormEntryCollectionElement;
  }
}
